body {
  background-color: #ABCDEF;
}

#start {
  color: lightcoral;
}

#end {
  color: gray;
}

.time-table {
  display: grid;
  grid-template-areas: 'start end';
  justify-content: center;
  gap: 20px;
}

.start-table {
  grid-area: start;
  white-space: pre-line;
  background-color: #9dbada;
  border-radius: 20px;
  padding: 30px;
}

.end-table {
  grid-area: end;
  white-space: pre-line;
  background-color: #9dbada;
  border-radius: 20px;
  padding: 30px;
}

button {
  background-color: #b5daff;
  padding: 10px;
  border: 0px;
  border-radius: 5px;
  box-shadow: 0 0 5px gray;
  font-weight: bold;
}

button:hover {
  background-color: #d3e9ff;
}

ul {
  padding-left: 10%;
}

li {
  border-radius: 5px;
  padding: 2px;
}
li:hover {
  background-color: #ABCDEF;
}